import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { GoodsListComponent } from './goods-list/goods-list.component';
import { CreateGoodsComponent } from './create-goods/create-goods.component';
import { EditGoodsComponent } from './edit-goods/edit-goods.component';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
    {path: 'create', component: CreateGoodsComponent},
    {path: 'edit/:id', component: EditGoodsComponent},
    {path: '', redirectTo: '/app/goods/list', pathMatch: 'full'},
    {path: 'list', component: GoodsListComponent},
];

@NgModule({
    declarations: [GoodsListComponent, CreateGoodsComponent, EditGoodsComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        CustomErrorModule,
        LoaderModule
    ]
})
export class GoodModule {
}
