import {Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import {IAllGood, IGoodsCategory} from '../../../interfaces/interfaces';
import Swal from 'sweetalert2';
import {AuthService} from '../../../services/auth.service';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup} from '@angular/forms';
import {GoodsService} from '../../../services/goods.service';
import {AppData} from '../../../data';

// declare var $: any;

@Component({
    selector: 'app-goods-list',
    templateUrl: './goods-list.component.html',
    styleUrls: ['./goods-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class GoodsListComponent implements OnInit {

    public searchForm: FormGroup;
    public displayType = 'list';

    public goods: IAllGood[] = [];
    public searchText: string;
    searching: boolean;

    url: string;
    current_page: number;
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;

    checked?: boolean;
    errorMessage: string;
    private selectedGoods: IAllGood[];
    categories?: IGoodsCategory[] = [];
    processing = false;

    constructor(private formBuilder: FormBuilder,
                public goodsService: GoodsService,
                private route: ActivatedRoute,
                private router: Router,
                public http: HttpClient,
                private authService: AuthService) {
    }

    ngOnInit() {
        this.buildForm();
        this.loadCategories();
        this.submitSearch()
    }

    submitSearch(perpage: any = 10) {
        const searchText = this.searchForm.get('searchField').value;
        const fromDate = this.searchForm.get('fromDate').value;
        const toDate = this.searchForm.get('toDate').value;
        const category = this.searchForm.get('category').value;

        let param = '';
        if (searchText) {
            param += 'search=' + searchText;
        }

        if (fromDate) {
            if (param) {
                param += '&';
            }
            param += 'from_date=' + this.getTextFromDate(fromDate);
        }

        if (toDate) {
            if (param) {
                param += '&';
            }
            param += 'to_date=' + this.getTextFromDate(fromDate);
        }

        if (category) {
            if (param) {
                param += '&';
            }
            param += 'category=' + category;
        }

        const perPage = perpage;

        if (perpage) {
            if (param) {
                param += '&';
            }
            param += 'per_page=' + perPage;
        }

        if (param) {
            param = '?' + param;
        }

        this.searchGoods('', param);
    }

    searchGoods(url, params) {
        this.searching = true;
        this.errorMessage = '';

        this.goods = [];
        this.last_page = -1;
        this.goodsService.searchGoods(url, params).subscribe((data: any) => {

                const arr = ((typeof data.data) !== 'undefined' && data.data.length > 0);
                if (arr) {
                    data.data.forEach(d => d.selected = false);
                }
                this.goods = data.data;
                this.current_page = data.current_page;
                this.from = data.from;
                this.last_page = data.last_page;
                this.next_page_url = data.next_page_url;
                this.path = data.path;
                this.prev_page_url = data.prev_page_url;
                this.per_page = data.per_page;
                this.to = data.to;
                this.total = data.total;

                this.searching = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.searching = false;
            }
        )
    }

    private buildForm() {
        this.searchForm = this.formBuilder.group({
            searchField: [''],
            fromDate: [''],
            toDate: [''],
            category: ['']
        });
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get permissions() {
        return AppData.permissions;
    }

    deleteGood(good) {
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete ' + good.name + '?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.goodsService.deleteGood(good.id).subscribe((response) => {
                    this.goods.splice(this.goods.indexOf(good), 1);
                }, (err) => {
                    this.errorMessage = Constants.handleError(err, this.router);
                });
            }
        })
    }

    get hasSomeItemsChecked() {
        return this.goods.filter(x => x.selected).length;
    }

    checkAll(ev) {
        this.goods.forEach(x => x.selected = ev.target.checked);
    }

    isAllChecked() {
        return this.goods.every(_ => _.selected);
    }

    public toggle(type) {
        this.displayType = type;
    }

    deleteSelectedGoods() {
        this.selectedGoods = this.goods.filter(_ => _.selected);
        if (this.selectedGoods == null || this.selectedGoods.length < 1) {
            return;
        }
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete the selected goods items (' + this.selectedGoods.length + ') ?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                const selectedItemIds = [];
                for (const item of this.selectedGoods) {
                    selectedItemIds.push(item.id);
                }

                this.errorMessage = '';
                this.processing = true;
                this.goodsService.deleteGoods(selectedItemIds).subscribe(data => {
                        for (const item of this.selectedGoods) {
                            this.selectedGoods.splice(this.selectedGoods.indexOf(item), 1);
                        }
                        this.processing = false;
                    }, (err) => {
                        this.errorMessage = Constants.handleError(err, this.router);
                        this.processing = false;
                    }
                )
            }
        })
    }

    private getTextFromDate(date: any) {
        if (typeof date === 'object') {
            return date.year + '-' + date.month + '-' + date.day;
        }
        return date.toString();
    }

    private loadCategories() {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) => {
            this.categories = categories;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }

}
