import {ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {IAllGood, IGoodPropertyTemplate, IGoodsCategory} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {GoodsService} from '../../../services/goods.service';
import {AuthService} from '../../../services/auth.service';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
    selector: 'app-edit-goods',
    templateUrl: './edit-goods.component.html',
    styleUrls: ['./edit-goods.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditGoodsComponent implements OnInit {

    public editForm: FormGroup;
    public propertyTemplateForm: FormGroup;
    errorMessage?: string;
    saving: boolean;
    loading = false;
    updating = false;
    categories?: IGoodsCategory[] = [];
    timer?: any;
    good: IAllGood = null;
    properties?: IGoodPropertyTemplate[] = [];

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private changeDetectorRef: ChangeDetectorRef,
                private elem: ElementRef,
                public http: HttpClient,
                private authService: AuthService,
                private goodsService: GoodsService) {
    }

    ngOnInit() {
        const id = this.route.snapshot.params['id'];
        this.timer = null;
        this.saving = false;
        this.buildForm();
        this.getCategories();
        this.fetchDetail(id);
    }


    private buildForm() {
        this.editForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required])],
            category_id: ['', Validators.compose([Validators.required])],
            short_description: ['', Validators.compose([Validators.required])]
        });
    }

    fetchDetail(id: number) {
        this.loading = true;
        this.goodsService.getGoodsDetail(id).subscribe((good: IAllGood) => {
                this.good = good;
                this.editForm.get('name').setValue(this.good.name);
                this.editForm.get('category_id').setValue(this.good.category_id);
                this.editForm.get('short_description').setValue(this.good.short_description);
                this.properties = [];
                if (this.good.properties_template) {
                    this.good.properties_template.forEach(property => {
                        this.properties.push({name: property.name, type: property.values.join(',')});
                    });
                }
                this.loading = false;
            }, err => {
                this.loading = false;
                this.errorMessage = Constants.handleError(err, this.router);
            }
        );
    }

    private prepareSave() {
        const input = this.editForm.value;
        input.properties = JSON.stringify({data: this.properties});
        return input;
    }

    public getCategories(): void {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) =>
            this.categories = categories
        );
    }

    addNewProperty() {
        this.properties.push({name: '', type: ''});
    }

    removeProperty(property?: IGoodPropertyTemplate) {
        this.properties.splice(this.properties.indexOf(property), 1);
    }

    onSubmit() {
        if (this.good == null) {
            return;
        }
        const formModel = this.prepareSave();
        this.saving = true;

        this.errorMessage = '';
        this.goodsService.updateGoods(formModel, this.good.id).subscribe((res: any) => {
            Swal.fire('Successful!', res.message, 'success');
            this.saving = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.saving = false;
        });
    }

    onApprove() {
        if (this.good == null || this.good.approval_status) {
            return;
        }

        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to approve this goods item?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Continue',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.updating = true;

                this.errorMessage = '';
                this.goodsService.approveGoods(this.good.id).subscribe((res: any) => {
                    Swal.fire('Successful!', res.message, 'success');
                    this.good = res.data;
                    this.updating = false;
                }, (err) => {
                    this.errorMessage = Constants.handleError(err, this.router);
                    this.updating = false;
                });
            }
        })
    }

}
