import {ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {IGood, IGoodPropertyTemplate, IGoodsCategory} from '../../../interfaces/interfaces';
import {ActivatedRoute, Router} from '@angular/router';
import {GoodsService} from '../../../services/goods.service';
import {HttpClient} from '@angular/common/http';
import {AuthService} from '../../../services/auth.service';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-create-goods',
    templateUrl: './create-goods.component.html',
    styleUrls: ['./create-goods.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateGoodsComponent implements OnInit {

    public newForm: FormGroup;
    public propertyForm: FormGroup;
    errorMessage?: string;
    saving: boolean;
    good?: IGood;
    savingNewGoods = false;
    categories?: IGoodsCategory[] = [];
    timer?: any;
    properties?: IGoodPropertyTemplate[] = [];
    propertyVal = '';

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private goodsService: GoodsService,
                private changeDetectorRef: ChangeDetectorRef,
                private elem: ElementRef,
                public http: HttpClient,
                private authService: AuthService) {
    }

    ngOnInit() {
        this.timer = null;
        this.saving = false;
        this.buildForm();
        this.getCategories();
    }


    private buildForm() {
        this.newForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required])],
            category_id: ['', Validators.compose([Validators.required])],
            short_description: ['', Validators.compose([Validators.required])]
        });
    }

    private prepareSave() {
        const input = this.newForm.value;
        input.properties = JSON.stringify({data: this.properties});
        return input;
    }

    public getCategories(): void {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) => {
                this.categories = categories;
            }, (err) => {
                Constants.handleError(err, this.router, true);
            }
        );
    }

    onSubmit() {
        const formModel = this.prepareSave();
        this.saving = true;

        this.errorMessage = '';
        this.goodsService.addNewGood(formModel).subscribe((res: any) => {
            this.newForm.reset();
            this.properties = [];
            Swal.fire('Successful!', res.message, 'success');
            this.saving = false;
        }, (error) => {
            Constants.handleError(error, this.router, true);
            this.saving = false;
        });
    }

    addNewProperty() {
        this.properties.push({name: '', type: ''});
    }

    removeProperty(property?: IGoodPropertyTemplate) {
        this.properties.splice(this.properties.indexOf(property), 1);
    }

    showProperties(cCategory) {
        const list = [];
        if (cCategory) {
            this.categories.forEach(category => {
                if (category.id.toString() === cCategory.toString()) {
                    category.properties_template.forEach(t => {
                        list.push(t.name);
                    });
                }
            });
        }

        if (list.length === 0) {
            this.propertyVal = '';
        } else {
            this.propertyVal = '<small class="mt-2">Properties: <strong>' + list.join(', ') + '</strong></small>';
        }
    }

}
